import {mapGetters, mapState} from 'vuex';
import appCategoryList from '../../../components/page-component/app-category-list/app-category-list.vue';

export default {
    components: {
        'app-category-list': appCategoryList,
    },
    data() {
        return {
            scrollTop: 0,
            list: [],
            goods: [],
            request: true,
            page: 1,
            classId: 0,
            activeIndex: 0,
            activeIndexTwo: 0,
            height: 0,
            over: false,
            scrollHeight: 0,
            mch_id: 0,
            cat_style: 0,
            cat_id: 0,
        }
    },
    computed: {
        ...mapGetters('mallConfig', {
            tabBarNavs: 'getNavBar'
        }),
        ...mapState('gConfig', {
            windowHeight: state => state.systemInfo.windowHeight,
            windowWidth: state => state.systemInfo.windowWidth,
        }),
        ...mapGetters('iPhoneX', {
            botHeight: 'getBotHeight'
        }),
        setHeight() {
            return this.windowHeight + 'px';
        }
    },
    onReachBottom() {

    },
    /**
     * 生命周期函数--监听页面加载
     */
    onLoad(options) { this.$commonLoad.onload(options);
        const self = this;
        self.mch_id = options.mch_id;
        self.$request({
            url: self.$api.mch.cat_style,
            data: {
                mch_id: self.mch_id,
            }
        }).then(info => {
            if (info.code === 0) {
                self.cat_style = info.data.setting.cat_style;
                self.loadData();
            }
        });
    },
    methods: {
        loadData: function () {
            const self = this;
            self.$showLoading();
            self.$request({
                url: self.$api.mch.cats_list,
                data: {
                    mch_id: self.mch_id,
                    cat_id: self.cat_id
                }
            }).then(info => {
                self.$hideLoading();
                if (info.code === 0) {
                    self.list = info.data.list
                }
            }).catch(info => {
                self.$hideLoading();
            })
        },
        // 点击导航栏
        active(item) {
            this.page = 1;
            uni.showLoading({
                text: '加载中...',
                mask: true
            });
            for (let i = 0; i < this.list.length; i++) {
                if (this.list[i].active !== item.active) {
                    this.list[i].active = false;
                }
                if (this.list[i].id === item.id) {
                    this.list[i].active = true;
                }
            }
            this.$request({
                url: `${this.$api.default.goods_list}&page=${this.page}&cat_id=${item.id}`
            }).then(response => {
                uni.hideLoading();
                this.goods = response.data.list;
                this.page = 1;
                this.classId = item.id;
                this.over = false;
            });
            this.over = false;
        },
        // 下拉分页请求
        req() {
            this.$request({
                url: `${this.$api.default.goods_list}&page=${this.page}&cat_id=${this.classId}`
            }).then(response => {
                if (response.data.list.length > 0) {
                    this.goods.push(...response.data.list);
                } else {
                    this.over = true;
                }
                uni.hideLoading();
            });
        },
        activeThree(item) {
            for (let i = 0; i < this.list.length; i++) {
                if (this.list[i].active !== item.active) {
                    this.list[i].active = false;
                }
                if (this.list[i].id === item.id) {
                    this.list[i].active = true;
                    this.activeIndex = i;
                }
            }
        }
    },
}
